@import "./../../styles/vars";

.nav-bar{
    position: absolute;
    top: 0;
    padding: 40px 0;
    width: 100%;
    z-index: 2;
}

.nav__btn{
    position: relative;
    color: $gray;
    &:hover{
        color: $accent;
    }
    transition: $transition;
    &::before{
        position: absolute;
        content: "";
        bottom: -7px;
        left: 50%;
        transform: translateX(-50%);
        width: 0;
        height: 2px;
        border-radius: 8rem;
        background-color: $accent; 
        transition: $transition;
    }
    &:not(.active-page):hover{
        transform: translate(0, -2px);
        &::before{
            width: 50%;
            
        } 
    }
}

.active-page{
    &::after{
        position: absolute;
        content: "";
        bottom: -7px;
        left: 50%;
        transform: translateX(-50%);
        width: 5px;
        height: 5px;
        border-radius: 50%;
        background-color: $gray; 
        transition: $transition;
    } 
    &:hover::after{
        height: 2px;
        width: 50%;
        border-radius: 8rem;
       background-color: $accent;
    }
}

.nav__wrapper{
    display: flex;
    justify-content: space-between;
}

