@import "./../../styles/vars";

.services{
    min-height: 100vh;
    padding: 20vh 0;
    display: grid;
    place-content: center;
    background-color: $black;
    color: $white;
}

.services__title{
    margin-bottom: 8vh;
    font-size: calc(clamp(3.25em,7vw,8em)*0.5);
}

.services__wrapper{
    display: flex;
    column-gap: 10vw;
}

.services__item{
    display: flex;
    flex-direction: column;
    row-gap: 8vh;
}

.service__name{
    position: relative;
    &::after{
        position: absolute;
        content: attr(data-number);
        right: 0;
        bottom: 0;
        width: 100%;
        text-align: end;
        font-size: calc(clamp(3.25em,7vw,8em)*0.2);
        transform: translate(0, 110%);
        color: $white-trans;
        border-top: 1px $white-trans solid;
    }
}