@import "./vars";

body {
  background-color: $white;
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
}

.container {
  padding: 0 30px;
  margin: 0 auto;
  max-width: 1500px;
  @media (max-width: 1600px){
    max-width: 1200px;
  }
}

.btn{
    padding: 5px 0;
}

.arr-btn{
  position: relative;
  padding: 10px 30px 10px 67px;
  width: fit-content;
  height: fit-content;
  border-radius: 10px;
  border: 1px $black solid;
  color: $black;
  text-transform: uppercase;
  transition: $transition;
  &::before{
    position: absolute;
    content: "";
    width: 12px;
    height: 12px;
    left: 35px;
    top: 50%;
    transform: translate(0, -50%);
    background-image: url(./../../public/imgs/icons/arrow.svg);
    background-size: cover;
    filter: invert(1);
    transition: $transition;
  }
  &:hover{
    color: $white;
    background-color: $accent;
    border-color: $accent;
  }
  &:hover::before{
    filter: invert(0);
    transform: translate(0, -50%) rotate(45deg); 
  }

}

.title{
  font-family: 'Oswald', sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  line-height: 0.9;
  font-size: calc(clamp(3.25em,7vw,8em)*1.6);
}

.subtitle{
  font-family: 'Oswald', sans-serif;
  font-weight: 500;
  font-size: calc(clamp(3.25em,7vw,8em)*0.3);
}

.text{
  font-size: 1em;
  font-weight: 400;
}