@import "./../../styles/vars";

.intro{
    padding: 10vh 0;
    min-height: 100vh;

    display: flex;
    justify-content: center;
    align-items: center;

    background-color: $black;
    color: $white;
}

.intro__wrapper{
    width: 100%;
}

.intro__title{
    
    &:nth-child(1){
        text-align: left;
    }
    &:nth-child(2){
        margin-bottom: 12vh;
        position: relative;
        text-align: right;
        z-index: 1;
        &::after{
            position: absolute;
            content: "&";
            font-family: "Dela Gothic One";
            font-weight: 400;
            left: 60%;
            bottom: 0;
            color: $white-trans;
            font-size: calc(clamp(3.25em,7vw,8em)*0.8);
            z-index: -1;
        }
    }
}

.intro__add{
    display: flex;
    justify-content: space-between;

    .arr-btn{
        align-self: flex-end;
        color: $white;
        border-color: $white;
        &:hover{
            border-color: $accent;
        }
        &::before{
            filter: invert(0);
        }
    }
}

.intro__desc{
    width: 30vw;
    
    & p:nth-child(1){
        margin-bottom: 20px;
    }
}
