@import "./../../styles/vars";

.projects{
    padding: 20vh 0;
    min-height: 100vh;
}


.projects__latest{
    margin-bottom: 5vh;
    position: relative;
    max-width: 100%;
    .project__spec{
        position: absolute;
        padding: 5vh 3vw;
        height: fit-content;
        width: 100%;
        bottom: 0;
        display: flex;
        justify-content: space-between;
        color: white;
        z-index: 2;
    }
    .arr-btn{
        align-self: flex-end;
        color: $white;
        border-color: $white;
        &:hover{
            border-color: $accent;
        }
        &::before{
            filter: invert(0);
        }
        
    }
    &::before{
        position: absolute;
        content: "";
        width: 100%;
        height: 100%;
        background: radial-gradient(165.75% 70.71% at 0% 100.00%, #222 0%, rgba(34, 34, 34, 0.00) 100%);
    }
}

.projects__column{
    .project__spec{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    .project__link{
        align-self: flex-start;
    }
}

.projects__column-wrapper{
    padding: 5vh 0;
    display: grid;
    grid-template: 1fr / 1fr 1fr ;
    column-gap: 3vw;
    border-width: 1px 0px 0px 0px;
    border-color: $black-trans;
    border-style: solid;
    
}

.projects__row{
    padding: 5vh 0;
    display: grid;
    grid-template: 1fr / 1fr 1fr ;
    column-gap: 3vw;
    border-width: 1px 0px 0px 0px;
    border-color: $black-trans;
    border-style: solid;

    .project__spec{
       display: flex;
       flex-direction: column;
       row-gap: 5vh;
    }
    .project__img{
        margin-bottom: 5vh;
    }
}


.project__title{
    font-size: calc(clamp(3.25em,7vw,8em)*0.5);
}

.project__year{
    margin-top: 1vh;
}

.project__img{
    display: block;
    box-shadow: 5px 5px 10px $black-trans;
    overflow: hidden;
    img{
        transition: $transition;
    }
    &:hover img{
        transform: scale(1.02);
    }
}

