@import "./../../styles/vars";



.about-hero{
    position: relative;
    padding-top: 10vh;
    min-height: 100vh;
    &::after{
        position: absolute;
        content: "";
        background: linear-gradient(180deg, rgba(0, 0, 0, 0.1) 0%, rgba(0, 0, 0, 0.2) 100%);
        width: 40%;
        right:  0;
        top: 0;
        height: 100%;
        z-index: -1;
    }
}
.about-hero__wrapper{
    display: flex;
    justify-content: space-between;
}
                  
.about-hero__content{
    min-height: 90vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.about-hero__title{
    margin-bottom: 15vh;
}
.about-hero__desc{
    max-width: 25vw;
    &:nth-child(2){
        margin-bottom: 20px;
    }
}
                    

.about-hero__photo{
    max-height: 90vh;
    justify-self: end;
    img{
        max-height: 100%;
        width: 100%;
        object-fit: contain;
        filter: grayscale(1)  brightness(0.9) drop-shadow(0px 0px 100px rgba(0, 0, 0, 0.2));
    }
}