@import "./../../styles/vars";

.contacts{
    padding: 20vh 0;
    min-height: 100vh;
    background-color: $black;
    color: $white;
    display: grid;
    place-content: center;
}

.contacts__title{
    margin-bottom: 12vh;
}

.contacts__wrapper{
    display: grid;
    grid-template: 1fr / 1fr 1fr
}

.contacts__desc{
    max-width: 50%;
    display: flex;
    flex-direction: column;
    row-gap: 20px;
}

.contacts__num{
    margin-bottom: 3vh;
}

.contacts__links{
    margin-bottom: 10vh;
    display: flex;
    column-gap: 5vw;
    a{
        color: $white;
    }
}

.contacts__mail{
    color: $white;
}