@import "./../../styles/vars";

.recent{
    padding: 20vh 0;
    min-height: 100vh;
}

.recent__title{
    margin-bottom: 12vh;
    line-height: 0.9;
    b{
        color: $black-trans;
    }
}

.recent__work{
    display: flex;
   justify-content: center;
    column-gap: 1vw;
}
.recent__item{
    position: relative;
    margin-bottom: 1vw;
    width: 100%;
    aspect-ratio: 3 /2;
    &:not(:last-child) .recent__desc{
      top: 0;
    }
    &:last-child .recent__desc{
      bottom: 0;
    }
    &:hover:not(:last-child){
      .recent__desc{
        transform: translate(-50%, -7vh);
      }
    }
    &:hover:last-child{
      .recent__desc{
        transform: translate(-50%, 7vh);
      }  
    }
}

.recent__desc{
  position: absolute;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 95%;
  left: 50%;
  transform: translate(-50%, 0);
  z-index: -1;
  transition: $transition;
}

.recent-project__img{
    display: block;
    box-shadow: 5px 5px 10px $black-trans;
    overflow: hidden;
    width: 100%;
    height: 100%;
    
    transition: $transition;
    &:active {
        transform: scale(0.98);
      }
    
    img{
        height: 100%;
        object-fit: cover;
        transition: $transition;
        &:hover{
            transform: scale(1.02);
            
        }
    }
}

.project__btn{
    position: relative;
    height: 100%;
    width: 100%;
    background-color: $black;
    color: $white;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 5px 5px 10px $black-trans;
    overflow: hidden;
    transition: $transition;
      
      &::before {
        content: '';
        position: absolute;
        inset: 0;
        margin: auto;
        width: 50px;
        height: 50px;
        border-radius: 50%;
        transform: scale(0);
        z-index: -1;
        background-color: $accent;
        transition: $transition;
      }
      &:hover::before {
        transform: scale(17);
      }
      &:hover {
        // color: $black;
        transform: scale(1);
      }
      &:active {
        transform: scale(0.98);
      }
}

.recent__left-col{
    margin-top: 20%;
}